@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,600;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;900&display=swap);
:root {
  --header-height: 3.5rem;
  --heart-height: 45px;

  /* COLORS */
  --first-color:#824D3E;
  --second-color: #C42130;
  --third-color: #444444;
  --discount-color: #EBD7CE;
  --title-color: #161212;
  --card-color: #EEEEEE;
  --tag-color: #824D3E;
  --title-home-color: #5361FF;
  --text-color: #666666;
  --body-color: ##E5E5E5;
  --white-color: #FFF;

    /* FONT WEIGTHT */
    --font-semi-bold: 600;
    --font-bold: 700;
    --font-bolder: 900;

  /* FONT AND SIZE */
  --first-font: 'Nunito', sans-serif;
  --second-font: 'Montserrat', sans-serif;
  --font-h2: 1.24rem;
  --font-h3: 1.124rem;
  --font-icon: 1.5rem;
  --little-font: .937rem;
  --small-font: .814rem;
  --normal-font: 1.3rem;
  --big-font: 2rem;

  /*Z-INDEX */

  --z-index-1: 1;
  --z-index2: 2;
  --z-index-fixed: 100;

  /* MARGINS */
  --mg-05: .5rem;
  --mg-08: .8rem;
  --mg-1: 1rem;
  --mg-2: 2rem;
  --mg-3: 4rem;
  --mg-4: 5rem;
  --mg-5: 6.5rem;
  --mg-6: 8rem;
  --mg-7: 10rem;
  --mg-8: 1.3rem;
}

/* BASE */
*,::before,::after {
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', sans-serif;
  font-family: var(--first-font);
  margin: 0px;
  align-items: center;
}

h1,h2,h3{
  color: #161212;
  color: var(--title-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

h1,h2,h3,ul,li,p {
  margin: 0;
}

ul, li {
  list-style: none;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  font-family: 'Nunito', sans-serif;
  font-family: var(--first-font);
  font-size: .937rem;
  font-size: var(--little-font);
  background: inherit;
  border: none;
  border-radius: inherit;
}

/* Header */

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-index2);
  background-color: var(--third-color);
}

.show__cart {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 2rem;
}

/* NavHeader */

.nav {
  display: flex;
  align-items: center;
  height: var(--header-height);
  justify-content: space-between;
  margin: 0 var(--mg-3);
  max-width: 1200px;
  margin: 0 auto;
}

.nav__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-left: 2rem;
}

.nav__logo h2 {
  color: var(--white-color);
  font-family: var(--logo-font);
  font-size: var(--font-h2);
  font-weight: var(--font-bolder);
}

.nav__logo h2:hover {
  color: var(--title-home-color);
}

.nav__logo img {
  margin-right: var(--mg-05);
  width: var(--mg-8);
}

.nav__icon {
  color: var(--white-color);
  cursor: pointer;
  font-size: var(--font-icon);
  font-weight: var(--font-semi-bold);
  margin-left: var(--mg-1);
  margin-top: var(--mg-05);
  transition: .5s;
}

.nav__favorites {
  display: flex;
  align-items: center;
  color: var(--white-color);
  justify-content: center;
}

.nav__icon:hover {
  color: var(--title-home-color);
}

.nav__list {
  display: flex;
  align-items: center;
}

.cart__count {
  background-color: var(--discount-color);
  border-radius: 50%;
  color: var(--title-color);
  font-size: var(--small-font);
  font-weight: var(--font-bold);
  min-width: 1.1rem;
  left: -5px;
  min-height: 1.1rem;
  position: relative;
  text-align: center;
  top: -11px;
  transition: 0.5s;
  z-index: var(--z-index-fixed);
}

.cart__count:hover {
  background-color: var(--white-color);
  color: var(--title-home-color);
}

@media screen and (min-width: 768px){
  .show__cart {
    margin-right: 5.3rem;
  }

  .nav__logo {
    margin-left: 6.4rem;
  }
}


.tag__summer, .tag__launch, .tag__discount {
  border: 1px solid var(--text-color);
  color: var(--text-color);
  font-size: .8rem;
  padding: .3rem;
}

.tag__discount {
  background-color: var(--discount-color);
  border: var(--discount-color) 1px solid;
}

.tag__launch {
  background-color: var(--tag-color);
  border: var(--tag-color) 1px solid;
  color: var(--white-color);
}

.render__products {
  display: flex;
  align-items: center;
  background-color: var(--white-color);
  border: var(--card-color) 1px solid;
  flex-direction: column;
  height: 430px;
  justify-content: center;
  margin-bottom: var(--mg-4);
  margin-left: var(--mg-2);
  margin-right: var(--mg-2);
  transition: .5s;
  /* width: 250px; */
}

.render__products:hover {
  height: 470px;
  margin-bottom: 0;
}

.render__products:hover .product__button {
  opacity: 1;
}

.render__products:hover .favorite__icon {
  margin-top: -3rem;
}

.favorite__icon, .title__products, .product__button {
  width: calc(100% - 1rem);
}

.favorite__icon {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -0.5rem;
  transition: .5s;
}

.favorite_button {
  color: var(--second-color);
  cursor: pointer;
  font-size: 1.6rem;
}

.title__products {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}

.image__product {
  align-self: center;
  height: 285px;
}

.title__product, .instalments__product {
  color: var(--text-color);
  font-size: var(--small-font);
  letter-spacing: .1rem;
  margin-top: 3px;
  transition: .5s;
}

.title__product {
  align-self: flex-start;
  margin-left: .5rem;
}

.info__products {
  align-self: flex-start;
  margin-left: 1rem;
}

.price__product {
  font-size: calc(var(--small-font) + 2px);
  font-weight: var(--font-bold);
  margin-top: var(--mg-05);
}

.product__button {
  margin-bottom: -3rem;
  opacity: 0;
  transition: .5s;
}

.buy__button {
  cursor: pointer;
  background-color: var(--first-color);
  color: var(--white-color);
  font-family: var(--second-font);
  font-weight: var(--font-semi-bold);
  letter-spacing: 1px;
  padding: .75rem 0;
  width: 100%;
}

.buy__button:hover {
  color: var(--title-home-color);
}

.all_products {
  max-width: 1200px;
}

@media screen and (min-width: 768px){
  .render__products {
    margin-left: var(--mg-1);
    margin-right: 0;
  }
}


.featured__products {
  display: grid;
  justify-content: center;
  margin: 2rem 1rem 0 4rem;
  /* width: 50vh; */
}

.right__content {
  margin-top: -3rem;
}

.featured__images img {
  width: 100%;
  margin-left: 2.5rem;
}

.title__images p {
  position: relative;
  color: var(--white-color);
  top: 10rem;
  font-family: var(--second-font);
  font-size: 1.3rem;
  font-weight: var(--font-bold);
  left: 5rem;
  letter-spacing: .35rem;
}

.featured__title {
  font-family:var(--first-font);
  letter-spacing: .10rem;
  margin-bottom: var(--mg-1);
  text-align: center;
}

@media screen and (min-width: 768px){

.featured__products {
    margin: 2rem auto;
  }

.featured__container {
  display: flex;
  justify-content: center;
}

.right__content {
  margin-left: .2rem;
  margin-top: 25px;
}

  .title__images p {
    top: 11.5rem;
  }

  .featured__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: -2rem auto 0 auto;
  }
  
  .featured__images img {
    width: inherit;
    margin-left: var(--mg-1);
    margin-right: 0;
    margin-top: 1.8rem;
  }
}

